import { useState, useEffect, useRef } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { ArrowRightCircle } from "react-bootstrap-icons";
import "animate.css";
import axios from "axios";

export const Banner = () => {
  const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const [text, setText] = useState("");
  const [delta, setDelta] = useState(150); // Typing speed
  const [data, setData] = useState({
    titles: ["AI Engineer", "ML Developer", "Computer Vision Specialist"],
    description: "As deeply passionate about leveraging cutting-edge technologies in artificial intelligence, machine learning, and computer vision to solve real-world problems. With hands-on experience in building and training machine learning models, designing innovative AI systems, and creating impactful solutions, I focus on driving efficiency and enhancing user experiences across various domains."
  }); // Default data in case API fails2
  const [loading, setLoading] = useState(true); // Loading state for the data
  const canvasRef = useRef(null);
  const animationRef = useRef(null);
  const mousePosition = useRef({ x: 0, y: 0 });

  // Fetch Banner Data
  useEffect(() => {
    const fetchBannerData = async () => {
      try {
        // Fetch the BannerDetails.json file directly from the raw URL
        const url = "https://raw.githubusercontent.com/guptamanya18/protfolio/json_data/BannerDetails.json";
        const response = await axios.get(url);

        setData(response.data); // Set the state with fetched data
        setLoading(false); // Set loading to false once data is fetched
      } catch (err) {
        console.error('Error fetching data from GitHub', err);
        setLoading(false); // Handle error and stop loading
      }
    };

    fetchBannerData();
  }, []);

  useEffect(() => {
    if (data && data.titles && data.titles.length > 0) {
      // Initialize text with the first character of the first title if text is empty
      if (text === "" && !isDeleting) {
        setText(data.titles[0].substring(0, 1));
      }
    } else {
      // If data or titles are not available, set text to empty
      setText("");  // Display nothing if no data or titles are available
    }

    const ticker = setInterval(() => {
      tick();
    }, delta);

    // Cleanup on unmount or dependency change
    return () => clearInterval(ticker);
  }, [text, delta, data, isDeleting, loopNum]); // Keep dependencies as before

  const tick = () => {
    if (!data || !data.titles || data.titles.length === 0) {
      return; // Prevent accessing data if titles are not available
    }

    const i = loopNum % data.titles.length;
    const fullText = data.titles[i];
    const updatedText = isDeleting
      ? fullText.substring(0, text.length - 1)
      : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (!isDeleting && updatedText === fullText) {
      // Pause at the end of the word
      setTimeout(() => {
        setIsDeleting(true);
        setDelta(100);
      }, 1000);
    } else if (isDeleting && updatedText === "") {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setDelta(150);
    }
  };

  // Interactive Star Background with Mouse Tracking
  useEffect(() => {
    const canvas = canvasRef.current;
    if (!canvas) return;

    const ctx = canvas.getContext('2d');
    const stars = [];
    const shootingStars = [];
    const starCount = 150; // More stars for denser background

    // Set canvas to full screen
    const resizeCanvas = () => {
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;
    };

    // Track mouse position
    const handleMouseMove = (e) => {
      mousePosition.current = {
        x: e.clientX,
        y: e.clientY
      };
    };

    window.addEventListener('resize', resizeCanvas);
    window.addEventListener('mousemove', handleMouseMove);
    resizeCanvas();

    // Create stars
    class Star {
      constructor() {
        this.x = Math.random() * canvas.width;
        this.y = Math.random() * canvas.height;
        this.size = Math.random() * 2 + 0.5;
        this.baseSize = this.size;
        this.speedX = (Math.random() - 0.5) * 0.3; // Slow horizontal drift
        this.speedY = (Math.random() - 0.5) * 0.3; // Slow vertical drift
        this.brightness = Math.random() * 0.5 + 0.5;
        this.pulseSpeed = Math.random() * 0.1 + 0.01;
        this.pulseFactor = 0;
      }

      update() {
        // Gently drift
        this.x += this.speedX;
        this.y += this.speedY;

        // Wrap around screen edges
        if (this.x < 0) this.x = canvas.width;
        if (this.x > canvas.width) this.x = 0;
        if (this.y < 0) this.y = canvas.height;
        if (this.y > canvas.height) this.y = 0;

        // Pulse size based on time
        this.pulseFactor += this.pulseSpeed;
        this.size = this.baseSize + Math.sin(this.pulseFactor) * 0.5;

        // React subtly to mouse within certain radius
        const dx = mousePosition.current.x - this.x;
        const dy = mousePosition.current.y - this.y;
        const distance = Math.sqrt(dx * dx + dy * dy);

        if (distance < 100) {
          // Grow slightly and brighten when mouse is near
          this.size = this.baseSize * 1.5;
          this.brightness = 1;
        } else {
          this.brightness = Math.random() * 0.5 + 0.5;
        }
      }

      draw() {
        ctx.fillStyle = `rgba(255, 255, 255, ${this.brightness})`;
        ctx.beginPath();
        ctx.arc(this.x, this.y, this.size, 0, Math.PI * 2);
        ctx.fill();
      }
    }

    // Create shooting stars
    class ShootingStar {
      constructor() {
        this.reset();
      }

      reset() {
        this.x = Math.random() * canvas.width;
        this.y = 0;
        this.length = Math.random() * 80 + 30;
        this.speed = Math.random() * 10 + 5;
        this.angle = Math.random() * Math.PI / 4 + Math.PI / 4; // Between 45-90 degrees
        this.opacity = 1;
        this.active = true;
      }

      update() {
        this.x += Math.cos(this.angle) * this.speed;
        this.y += Math.sin(this.angle) * this.speed;

        // Fade out as it moves
        this.opacity -= 0.01;

        if (this.opacity <= 0 || this.y > canvas.height || this.x > canvas.width) {
          // Only reset sometimes to make shooting stars rarer
          if (Math.random() < 0.02) {
            this.reset();
          } else {
            this.active = false;
          }
        }
      }

      draw() {
        if (!this.active) return;

        ctx.strokeStyle = `rgba(255, 255, 255, ${this.opacity})`;
        ctx.lineWidth = 2;
        ctx.beginPath();
        ctx.moveTo(this.x, this.y);
        ctx.lineTo(
          this.x - Math.cos(this.angle) * this.length,
          this.y - Math.sin(this.angle) * this.length
        );
        ctx.stroke();
      }
    }

    // Create stars
    for (let i = 0; i < starCount; i++) {
      stars.push(new Star());
    }

    // Create shooting stars
    for (let i = 0; i < 5; i++) {
      shootingStars.push(new ShootingStar());
    }

    // Add a new shooting star occasionally
    setInterval(() => {
      for (let star of shootingStars) {
        if (!star.active) {
          star.reset();
          break;
        }
      }
    }, 3000);

    const animate = () => {
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      // Draw stars
      for (let star of stars) {
        star.update();
        star.draw();
      }

      // Draw shooting stars
      for (let shootingStar of shootingStars) {
        if (shootingStar.active) {
          shootingStar.update();
          shootingStar.draw();
        }
      }

      animationRef.current = requestAnimationFrame(animate);
    };

    animate();

    return () => {
      window.removeEventListener('resize', resizeCanvas);
      window.removeEventListener('mousemove', handleMouseMove);
      cancelAnimationFrame(animationRef.current);
    };
  }, []);

  return (
    <section className="banner" id="home">
      <canvas ref={canvasRef} className="star-background"></canvas>

      <Container>
        <Row className="banner-row">
          <Col xs={12} md={6} xl={7} className="text-column">
            <div className="banner-content">
              <div className="tagline">LET'S Make the Future Brighter</div>
              <h1 className="banner-heading">Er. Manya Gupta</h1>
              {/* Fixed typing effect with more visible styling */}
              <div className="typing-effect">
                <span className="txt-rotate">
                  <span className="wrap">{text}</span>
                </span>
              </div>
              <p className="banner-description">
                {loading ? "Loading..." : data.description ||
                  "As deeply passionate about leveraging cutting-edge technologies in artificial intelligence, machine learning, and computer vision to solve real-world problems. With hands-on experience in building and training machine learning models, designing innovative AI systems, and creating impactful solutions, I focus on driving efficiency and enhancing user experiences across various domains."}
              </p>
              <div className="banner-buttons">
                <button className="btn-connect"
                  onClick={() => window.open('https://www.linkedin.com/in/rathoreatri03/', '_blank')}>
                  Let's Connect <ArrowRightCircle size={20} />
                </button>
                <button className="btn-projects"
                  onClick={() => document.getElementById('projects').scrollIntoView({ behavior: 'smooth' })}>
                  View Projects
                </button>
              </div>
            </div>
          </Col>

          <Col xs={12} md={6} xl={5} className="animation-column">
            <div className="floating-animation">
              <div className="glow-effect"></div>
              <div className="cube">
                <div className="face front"></div>
                <div className="face back"></div>
                <div className="face right"></div>
                <div className="face left"></div>
                <div className="face top"></div>
                <div className="face bottom"></div>
              </div>
              <div className="circle-orbit">
                <div className="circle c1"></div>
                <div className="circle c2"></div>
              </div>
              <div className="sphere"></div>
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  );
};