import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import 'animate.css';
import TrackVisibility from 'react-on-screen';

// ContactAnimation component to replace the static SVG
const ContactAnimation = () => {
  useEffect(() => {
    // Animation code runs after component mounts
    const animateEnvelopes = () => {
      const envelopes = document.querySelectorAll('.envelope');
      envelopes.forEach((envelope, index) => {
        // Add delay based on index for cascading effect
        setTimeout(() => {
          envelope.classList.add('animate');
        }, index * 200);
      });
    };

    animateEnvelopes();

    // Restart animation every 5 seconds
    const interval = setInterval(animateEnvelopes, 5000);

    return () => clearInterval(interval);
  }, []);

  return (
    <div className="contact-animation-container">
      <div className="contact-animation">
        {/* Main circle */}
        <div className="circle-container">
          <div className="circle pulse"></div>
        </div>

        {/* Envelopes circling around */}
        <div className="envelope envelope-1">
          <div className="envelope-top"></div>
          <div className="envelope-body"></div>
        </div>
        <div className="envelope envelope-2">
          <div className="envelope-top"></div>
          <div className="envelope-body"></div>
        </div>
        <div className="envelope envelope-3">
          <div className="envelope-top"></div>
          <div className="envelope-body"></div>
        </div>

        {/* Connection lines */}
        <div className="connection-lines">
          <div className="line line-1"></div>
          <div className="line line-2"></div>
          <div className="line line-3"></div>
        </div>
      </div>
    </div>
  );
};

export const Contact = () => {
  const formInitialDetails = {
    firstName: '',
    lastName: '',
    email: '',
    phone: '',
    message: ''
  }

  const [formDetails, setFormDetails] = useState(formInitialDetails);
  const [buttonText, setButtonText] = useState('Send');
  const [status, setStatus] = useState({});

  const onFormUpdate = (category, value) => {
    setFormDetails({
      ...formDetails,
      [category]: value
    })
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    setButtonText("Sending...");

    // Submit the form data to Formspree
    let response = await fetch("https://formspree.io/f/xjkwkoev", {
      method: "POST",
      headers: {
        "Content-Type": "application/json;charset=utf-8",
      },
      body: JSON.stringify(formDetails),
    });

    setButtonText("Send");
    setFormDetails(formInitialDetails);

    // Update the status message based on the response from Formspree
    if (response.ok) {
      setStatus({ success: true, message: 'Message sent successfully!' });
    } else {
      setStatus({ success: false, message: 'Something went wrong. Please try again later.' });
    }
  };

  return (
    <section className="contact" id="connect">
      <Container>
        <Row className="align-items-center">
          <Col size={12} md={6}>
            <TrackVisibility>
              {({ isVisible }) =>
                <div className={isVisible ? "animate__animated animate__zoomIn" : ""}>
                  <ContactAnimation />
                </div>
              }
            </TrackVisibility>
          </Col>
          <Col size={12} md={6}>
            <TrackVisibility>
              {({ isVisible }) =>
                <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                  <h2>Get In Touch</h2>
                  <form onSubmit={handleSubmit}>
                    <Row>
                      <Col size={12} sm={6} className="px-1">
                        <input
                          type="text"
                          value={formDetails.firstName}
                          placeholder="First Name"
                          onChange={(e) => onFormUpdate('firstName', e.target.value)}
                          required
                        />
                      </Col>
                      <Col size={12} sm={6} className="px-1">
                        <input
                          type="text"
                          value={formDetails.lastName}
                          placeholder="Last Name"
                          onChange={(e) => onFormUpdate('lastName', e.target.value)}
                          required
                        />
                      </Col>
                      <Col size={12} sm={6} className="px-1">
                        <input
                          type="email"
                          value={formDetails.email}
                          placeholder="Email Address"
                          onChange={(e) => onFormUpdate('email', e.target.value)}
                          required
                        />
                      </Col>
                      <Col size={12} sm={6} className="px-1">
                        <input
                          type="tel"
                          value={formDetails.phone}
                          placeholder="Phone No."
                          onChange={(e) => onFormUpdate('phone', e.target.value)}
                          required
                        />
                      </Col>
                      <Col size={12} className="px-1">
                        <textarea
                          rows="6"
                          value={formDetails.message}
                          placeholder="Message"
                          onChange={(e) => onFormUpdate('message', e.target.value)}
                          required
                        ></textarea>
                        <button type="submit">
                          <span>{buttonText}</span>
                        </button>
                      </Col>
                      {
                        status.message &&
                        <Col>
                          <p className={status.success === false ? "danger" : "success"}>{status.message}</p>
                        </Col>
                      }
                    </Row>
                  </form>
                </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
    </section>
  );
};